import * as React from 'react';
import * as ReactDOM from 'react-dom/client';
import singleSpaReact from 'single-spa-react';
import App from './App';
var lifecycles = singleSpaReact({
    React: React,
    ReactDOMClient: ReactDOM,
    rootComponent: App,
    errorBoundary: function (err, info, props) {
        console.error('Microfrontend Error:', err);
        return React.createElement('div', null, "Error in buying flow: ".concat(err.message));
    },
});
export var bootstrap = lifecycles.bootstrap, mount = lifecycles.mount, unmount = lifecycles.unmount;
// For standalone development
if (process.env.NODE_ENV === 'development' && !window.System) {
    var root = ReactDOM.createRoot(document.getElementById('root'));
    root.render(React.createElement(App));
}
